* {
  --white: #f6f6f6;
  --black: #151515;
  --primaryColor: #00c9e4;
  --accentColor: #d804a9;
}

html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

body {
  position: relative;
  overflow: scroll;
  overflow-x: hidden;
  margin: 0;
  padding: 0;

  background: var(--black);
  color: var(--white);
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  min-height: 500px;
}

a {
  text-decoration: none;
}

h1 {
  font-family: 'Domine', serif;
  font-size: 55px;
  font-weight: 300;
  margin: 45px 0 10px;
}

h2 {
  font-family: 'Domine', serif;
  font-size: 40px;
  font-weight: 300;
  text-align: center;
  margin: 45px 0 10px;
}

h3 {
  font-family: 'Domine', serif;
  font-size: 21px;
  font-weight: 300;
  margin: 0;
}

h4 {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  font-weight: 300;
  margin: 0;
}

p {
  text-align: center;
  font-size: 16px;
}

#root {
  background-repeat: no-repeat;
  background-color: var(--black);
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}

/* Home section styles */
#home {
  position: relative;
  overflow: visible;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100vw;
  min-height: 95vh;
}

#background {
  position: absolute;
  top: 0;
  width: 1000px;
}

#promo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#loveMeCover {
  width: 300px;
  height: 300px;
  position: absolute;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

#loveMeOutline {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 299px;
  height: 299px;
  fill: transparent;
  stroke: var(--accentColor);
  stroke-width: 5px;
  stroke-dasharray: 1200px;
  stroke-dashoffset: 1200px;
}

/* About section styles */
#about {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 90vw;
  padding-top: 50px;
}

#logo {
  width: 200px;
  height: 200px;

  user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
}

#logoOutlineSVG {
  width: 200px;
  height: 200px;
  stroke: var(--white);
  stroke-dasharray: 11000px;
  stroke-dashoffset: 0;
}

#tallPhoto {
  width: 90vw;
  margin-top: 50px;
}

#names {
  display: flex;
}

.name {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100px;
  margin: 10px 0;
}

/* Shows section styles */
#shows {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* align-content: stretch; */
  width: 90vw;
  padding-top: 50px;
}

/* Contact section styles */
#contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  z-index: 0;
}

#logoSmall {
  width: 100px;
  height: 100px;
}

#socialLinks {
  display: flex;
}

.emailButton {
  color: var(--white);
  border-color: var(--white);
  margin: 15px 0 50px 0;
}

.emailButton:hover {
  border-color: var(--accentColor);
  color: var(--accentColor);
}

.socialButton {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 20px;
  color: var(--white);
  font-size: 20px;
  text-align: center;
  transition: 0.25s;
}

.tailText {
  margin: 10px 0;
  text-align: center;
}

@media only screen and (min-width: 700px) {
  h3 {
    font-family: 'Domine', serif;
    font-size: 32px;
    font-weight: 300;
    margin: 0;
  }

  h4 {
    font-family: 'Montserrat', sans-serif;
    font-size: 21px;
    font-weight: 300;
    margin: 0;
  }

  /* Promo section styles */
  #home {
    height: 100vh;
    min-height: 675px;
    max-height: 800px;
  }

  #background {
    width: 2000px;
    top: -525px;
    left: -25px;
  }

  .buttonLabel:hover {
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.1);
    border-color: var(--accentColor);
    color: var(--accentColor);
  }

  /* About section styles */
  #about {
    max-width: 1024px;
  }

  #aboutSection {
    max-width: 1024px;
  }

  #widePhoto {
    width: 90vw;
    height: auto;
    margin-top: 50px;
    max-width: 1024px;
  }

  .name {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 40px 4vw;
    width: 125px;
  }

  .socialButton:hover {
    color: var(--accentColor);
  }
}
